.button-blue {
    color: #fff;
    border: 2px solid #2d6cc5;
    background-color: #398cff;
    .fa-spinner {
        animation-name: spin;
        animation-duration: 5000ms;
        animation-iteration-count: infinite;
        animation-timing-function: linear; 
        &:before {
            font-size: .9em;
        }
    }
    &:hover {
        background-color: #398cff;
    }
    &:active {
        background-color: #2d6cc5;
    }
}

@keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
}


.app-title-bar {
    padding: 21px;
    background-color: #006c91;
    span {
        display: block;
    }
    &__content {
        color: #fff;
        &__title {
            margin-bottom: 8px;
            font-size: 1.3em;
            font-weight: 600;
        }
        &__author {
            font-size: 1em;
        }

    }
}


.nav-bar {
    a {
        font-size: 1em;
        font-weight: 700;
        letter-spacing: 1.8px;
        text-transform: uppercase;
        text-decoration: none;
        color: #1e375a;
        -webkit-transition: .3s;
        -o-transition: .3s;
        transition: .3s;
        &:hover {
            color: #2364d2;
        }
    }
}

@mixin form-error-msg {
    border: 3px solid red;
    background-color: #ffe0e0;
    color: red;
    padding: 10px;
    font-size: 1em;
    font-weight: 600;
    text-align: left;
    line-height: 1.3em;
    margin-bottom: 20px;
}

@mixin form-nav {
    font-size: 1.2em;
    margin-bottom: 20px;
    font-weight: 600;
}

@mixin form-nav-links {
    text-decoration: none;
    color: #398cff;
}

@mixin form-nav-links-hover {
    color: #2364d2;
}

.form-intro-section {
    margin-bottom: 70px;
    padding: 20px;
    background-color: #ddd;
    &__content-wrapper {
        width: 830px;
        margin: 0 auto;
        padding: 30px;
        border: 5px solid #abab07;
        background-color: #525228;
        text-align: left;
        color: yellow;
    }
}

.allinone-auth {
    width: 500px;
    margin: 0 auto;
    display: block;
    .login-reg-form {
        &__component-error {
            @include form-error-msg;
        }
        &__header {
            text-align: left;
            &__title {
                margin-bottom: 10px;
                font-size: 1.6em;
                font-weight: 600;
            }
            &__description {
                margin-bottom: 20px;
                font-size: 1.1em;
                font-weight: 500;
                line-height: 1.4em;
                color: #1e1e1e;
            }
        }
        &__general-message {
            @include form-error-msg;
        }
        &__form {
            margin-bottom: 35px;
            &__input-wrapper {
                position: relative;
                height: 43px;
                margin-bottom: 25px;
            }
            &__input {
                width: 100%;
                padding: 9px 0px 9px 37px;
                font-size: 1.1em;
                &.invalid {
                    border-color: #ff0000;
                    background-color: #ffecec;
                }
            }
            &__input-icon {
                position: absolute;
                bottom: 15px;
                left: 13px;
                font-size: 1em;
            }
            &__input-icon-eye {
                position: absolute;
                bottom: 15px;
                right: 13px;
                font-size: 1em;
                cursor: pointer;
            }
            &__validation-msg {
                position: absolute;
                bottom: -17px;
                left: 0px;
                font-size: .8em;
                color: red;
            }
            // &__button-wrapper {}
            &__submit {
                width: 100%;
                padding: 9px;
                font-size: 1.1em;
            }
            &__reset-password, 
            &__back-to-login,
            &__create-account {
                @include form-nav;
                a {
                    @include form-nav-links;
                    &:hover {
                        @include form-nav-links-hover;
                    }
                }
            }
        }
    }
}

.home-logged-in-message {
    h2 {
        font-size: 2em;
        font-weight: 600;
        margin-bottom: 30px;
    }
    p {
        @include form-nav;
        a {
            @include form-nav-links;
            &:hover {
                @include form-nav-links-hover;
            }
        }
    }
}